import React, {useEffect, useState} from "react";
import {useWeb3} from "../../api/contracts";
import styled from 'styled-components';

import Tokenlist from "./tokenlist";
import TokenTable from "./TokenTable";

import Navbg from "../../assets/images/navBg.png";
import NavbgAct from "../../assets/images/navBgAct.png";
import ArrowTop from "../../assets/images/arrowTop.png"

const Main = styled('div')`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
 
`


const TableAll = styled('div')`
  flex-grow: 1;
  overflow-y: auto;
  width: 100%;
  height: calc(100vh - 4.2rem);

`

const NavBrdr = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgba(255, 249, 242, 0.2);
  padding-bottom: 0.32rem;
  width: 100%;

  ul{
    justify-content: flex-start;
    display: flex;
  }
  li{
    text-transform: capitalize;
    height: 0.44rem;
    background: url(${Navbg}) no-repeat;
    background-size: 100% 100%;
    text-align: center;
    line-height: 0.44rem;
    font-size: 0.12rem;
    padding: 0 0.25rem;
    margin-right: 0.32rem;
    opacity: 0.3;
    cursor: pointer;
    &.active{
     background: url(${NavbgAct}) no-repeat;
      background-size: 100% 100%;
      color: #FF9400;
      opacity: 1;
    }
  }
`;

const ArrowTopImg = styled('img')`
  width: 0.24rem;
  height: 0.24rem;
  cursor: pointer;
`;
export default function Token() {
    const {  dispatch } = useWeb3();
    const [list] = useState([{name:'Listed'}, {name:'Sale'},{name:'Transaction History'}]);
    const [curIndex, setCurIndex] = useState(0);
    const [showTop, setShowTop] = useState(true);

    useEffect(() => {
        dispatch({ type: 'SHOW_PAGE', payload: null });
    },[]);

    const handleSelect = (index) =>{
        setCurIndex(index)
    }
    const hanleShow = () =>{
        console.log("==showTop===",showTop)
        setShowTop(!showTop)
    }

    return <Main>
        {
            showTop &&<Tokenlist />
        }

        <TableAll>
            <NavBrdr>
                <ul>
                    {
                        list.map((item,index)=>(
                            <li className={curIndex ===index ?'active':''} onClick={()=>handleSelect(index)}><span>{item.name}</span></li>
                        ))
                    }
                </ul>
                <ArrowTopImg src={ArrowTop} alt="" onClick={()=>hanleShow()}/>
            </NavBrdr>
            <TokenTable />
        </TableAll>
    </Main>
}
