export default function GetMore1() {
    // const navigate = useNavigate();
    // const [list] = useState([{name:'Weapons'}, {name:'Equipments'},{name:'Reaction Devices'}]);
    const [list] = useState([{name:'Weapons'}, {name:'Equipments'}]);
    const [curIndex, setCurIndex] = useState(0);

    const handleSelect = (index) =>{
        setCurIndex(index)
    }

    // const ToUrl = () => {
    //     navigate('/market/2')
    // }

    const getRandomArrayElements = (arr,) => {
        let count;
        if( document.body.clientWidth >1000){
            count =5;
        }else{
            count =6;
        }
        let shuffled = arr.slice(0);
        let i = arr.length;
        let min = i - count;
        let temp;
        let index;
        while (i-- > min) {
            index = Math.floor((i + 1) * Math.random());
            temp = shuffled[index];
            shuffled[index] = shuffled[i];
            shuffled[i] = temp;
        }
        return shuffled.slice(min);
    }
    return <div>
        <NavBrdr>
            <Navlist list={list} handleSelect={handleSelect}/>
        </NavBrdr>
        {
            curIndex === 0 && <SwitchList>
                {
                    // weaponlist.map((item,index)=>(<li key={`weapon_${index}`}>
                    getRandomArrayElements(weaponlist).map((item,index)=>(<li key={`weapon_${index}`}>
                        <div><img src={homeling} alt=""/></div>
                        <Lv>{item.lv}</Lv>
                        <ImgBg>
                            <img src={item.img} alt=""/>
                        </ImgBg>
                        <Names>{item.name}</Names>
                        <Level>Level: <span>{item.level}</span></Level>
                        {/*<User>{item.user}</User>*/}
                        {/*<Address>X01232JFH2I3I23421HREI232FDFA2DFDF</Address>*/}
                        <StarV>
                            <Star value={item.star}/>
                        </StarV>
                    </li>))
                }


            </SwitchList>
        }
        {
            curIndex === 1 &&<SwitchList>
                {
                    getRandomArrayElements(armours).map((item,index)=>(<li key={`weapon_${index}`}>
                        <div><img src={homeling} alt=""/></div>
                        <Lv>{item.lv}</Lv>
                        <ImgBg>
                            <img src={item.img} alt=""/>
                        </ImgBg>
                        <Names>{item.name}</Names>
                        <Level>Level: <span>{item.level}</span></Level>
                        {/*<User>{item.user}</User>*/}
                        {/*<Address>X01232JFH2I3I23421HREI232FDFA2DFDF</Address>*/}
                        <StarV>
                            <Star value={item.star}/>
                        </StarV>
                    </li>))
                }
            </SwitchList>
        }
        <BtmBg>
            {/*<Partpanel onClick={()=>ToUrl()}>*/}
            {/*    GET MORE*/}
            {/*</Partpanel>*/}
            <Partpanel2  />
        </BtmBg>

    </div>
}
