import React, {useEffect, useState} from 'react';
import styled from 'styled-components';

import { useWeb3 } from "../../api/contracts";
import myaircraft from "../../json/myaircraft";
import ConfirmModal from "../modal/confirmModal";

import aircraftLft from '../../assets/images/aircraftLftbg.png';
import listli from '../../assets/images/listli.png';
import Bgbtn from '../../assets/images/btn80.png';
import BgbtnNor from '../../assets/images/btnWhite.png';

import PriceImg from '../../assets/images/price.png';


const Ul = styled.ul`
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
  margin-top: -0.6rem;
  li{
    box-sizing: border-box;
    width: 33.33333333%;
    text-align: center;
    height: 2.4rem;
    margin-top:0.6rem;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
  }
`;
const Imgs = styled.img`
    width: 1rem;
    height: 1rem;
    margin-bottom: 0.12rem;
`;

const Titles = styled.div`
    font-size: 0.16rem;
    font-family: "Orbitron-Medium";
    font-weight: 500;
    margin-bottom: 0.16rem;
`;

const SubTit = styled.div`
    font-family: 'orbitron-regular';
    font-size: 0.12rem;
    font-weight: 400;
    line-height:  2.4em;
    opacity: 0.6;
    margin:0.18rem 0 0.02rem;
`;

const Speed = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.14rem;
  span{
    display: inline-block;
  }
`;

const LastRow = styled.div`
  display: flex;
  justify-content: space-between;
  margin-top: 0.26rem;
`;

const RhtA = styled.a`
    width: 0.8rem;
    height:0.32rem;
    display: inline-block;
    background: url(${Bgbtn}) no-repeat;
    background-size: 100% 100%;
    line-height: 0.32rem;
    font-family: "Orbitron-Medium";
    font-size: 0.12rem;
    font-weight: 500;
    color: #0C0C0B;
    cursor: pointer;
`;

const RhtBtn = styled(RhtA)`
   background: url(${BgbtnNor}) no-repeat;
    background-size: 100% 100%;
    color: #FFF9F2;
`

const Price = styled.div`
    font-size: 0.16rem;
    font-family: "Orbitron-Medium";
    font-weight: 500;
    //color: #FF9400;
    line-height: 0.32rem;
    span{
    font-size: 0.12rem;
    font-family: 'orbitron-regular';
    font-weight: 400;
    }
`;

const LftPrice =styled.div`
  display: flex;
  align-items: center;
  img{
  width: 0.24rem;
  height: 0.24rem;
  margin-right: 0.08rem;
  }
`;

const Lilft = styled('div')`
    display: flex;  
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 1rem;
   margin-right: 0.16rem;
`;

const ImgsBg = styled('img')`
      width: 0.48rem;
      height: 0.48rem;
    
`;

const Lirht = styled('div')`
    flex-grow: 1;
    display: flex;  
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
`;

const ImgRht = styled('img')`
  width: 0.24rem;
  margin-bottom: 0.02rem;
`;

export default function MyAircraft() {
    const {  dispatch } = useWeb3();

    const [activeItem, setActiveItem] = useState(null);
    const [tips, setTips] = useState(null);

    useEffect(() => {
        dispatch({ type: 'SHOW_PAGE', payload: true });
    },[]);

    const SaleAircraft = (item) =>{
        dispatch({type: 'SHOW_MODAL',payload: true});
        setActiveItem(item)
        setTips("Confirm the sale of your aircraft .")
    }
    const CancelAircraft = (item) =>{
        dispatch({type: 'SHOW_MODAL',payload: true});
        setActiveItem(item)
        setTips("Are you sure you want to cancel the current aircraft sale ?")
    }

    return <div>
        <ConfirmModal item={activeItem} tips={tips} buttonTips="upload" />
        <Ul>
            {
                myaircraft.map((item,index)=>(
                    <li>
                        <Lilft>
                            <Imgs src={item.img} alt=""/>
                            <ImgsBg src={aircraftLft} alt=""/>
                            {
                                item.sale &&<RhtA onClick={()=>SaleAircraft(item)}>Sale</RhtA>
                            }
                            {
                                !item.sale &&<RhtBtn onClick={()=>CancelAircraft(item)}>Cancel</RhtBtn>
                            }

                        </Lilft>
                        <Lirht>
                            <Titles>{item.name}</Titles>
                            <ImgRht src={listli} alt="" />
                            <SubTit>Energy recovery speed</SubTit>
                            <Speed><span>{item.speed}</span></Speed>
                            <SubTit>Number of equipment</SubTit>
                            <Speed><span>{item.equipment}</span></Speed>
                            <LastRow>
                                <LftPrice>
                                    <img src={PriceImg} alt=""/>
                                    <Price>{item.price} <span>{item.symbol}</span></Price>
                                </LftPrice>
                            </LastRow>
                        </Lirht>
                    </li>
                ))
            }


        </Ul>
        </div>

}
